.receive {
  padding: 76px 0;
  padding-bottom: 84px;
  background: url("../img/services.jpg") no-repeat center;
  background-size: cover;

  &__main-title {
    font-weight: 600;
    font-size: calc(22px + 23 * (100vw / 1920));
    // font-size: 45px;
    margin-bottom: 76px;
    line-height: 100%;
  }

  &__title {
    font-weight: 600;
    font-size: calc(20px + 22 * (100vw / 1920));
    line-height: 130%;
    color: #0064a5;
    margin-bottom: 41px;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }

  &__item {
    flex: 0 1 470px;
  }

  &__text {
    font-size: calc(15px + 10 * (100vw / 1920));
    span {
      font-weight: 600;
    }
  }
}

@media(max-width: 768px) {
  .receive__main-title {
    margin-bottom: 40px;
  }
  .receive__wrap {
    flex-wrap: wrap;
  }
  .receive__item {
    flex: 0 1 100%;
  }
}

@media(max-width: 540px) {
  .receive__main-title {
    font-size: 22px;
    margin-bottom: 31px;
  }
  .receive {
    padding: 33px 0;
    background: url("../img/services-mobile.png") no-repeat center;
    background-size: cover;
  }
  .receive__title {
    font-size: 20px;
    margin-bottom: 14px;
  }
  .receive__text {
    font-size: 15px;
  }
  .receive__wrap  {
    gap: 32px;
  }
}
