.footer {
  background: url("../img/contact-bg-new.jpg") no-repeat center;
  background-size: cover;
    &__wrap {
    display: flex;
    gap: 20px;
  }

  &__item {
    flex: 0 1 770px;
    color: #000;
    font-size: calc(15px + 15 * (100vw / 1920));
    font-weight: 500;
    padding-top: calc(33px + 66 * (100vw / 1920));
  }

  &__title {
    font-weight: 700;
    font-size: calc(20px + 20 * (100vw / 1920));
    margin-bottom: calc(15px + 20 * (100vw / 1920));
    line-height: 125%;
  }

  &__item-content {
    margin-bottom: calc(25px + 38 * (100vw / 1920));
  }

  &__item-text:not(:last-child) {
    margin-bottom: 45px;
  }


  &__item-link {
    display: inline-block;
    flex: 0 0 244px;
  }

  iframe {
    width: 100%;
    height: 100%;
  }

  &__map {
    flex: 0 1 950px !important;
    height: calc(513px + 262 * (100vw / 1920)) !important;
  }
}

.footer {
  &__contact{
    font-weight: 600;
    font-size: calc(15px + 8 * (100vw / 1920));
  }
  &__contact-item {
    display: flex;
    gap: 20px;
  }
  &__contact-item:not(:last-child) {
    margin-bottom: 31px;
  }
  &__contact-name {
    flex: 0 0 244px;
  }
  &__contact-phone {
    font-family: var(--second-family);
    font-weight: 500;
  }
  &__contact-item--wrap {
    display: flex;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
  }
}

.footer__information {
  background: #5a5a57;
  padding: 30px 0;
  &-text {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: calc(10px + 5 * (100vw / 1920));
    line-height: 100%;
    color: #f1f3f4;
  }
}

@media(max-width: 1000px) {
  .footer__wrap {
    display: block;
  }
  .footer__container {
    padding: 0;
  }
  .footer__item {
    padding: 26px 20px;
  }
  .footer__map {
    height: calc(300px + 13.6458333333vw) !important;
  }
}

@media(max-width: 540px) {
  .footer {
    background: none;
  }
  .footer__item {
    color: #000;
    font-size: 15px;
    padding: 34px 20px;
    background: url("../img/contact-bg-mobile.jpg") no-repeat center;
    background-size: cover;
    margin: 0 -20px;
  }
  .footer__title {
    font-size: 20px;
    margin-bottom: 22px;
  }
  .footer__item-text {
    margin-bottom: 10px;
  }
  .footer__item-link {
    font-weight: 700;
    margin-bottom: 0px;
    margin-top: 0px;
    font-size: 15px;
    flex: 0 0 160px;
  }
  .footer__item-link:last-child {
    margin-top: 0;
  }
  .footer__item-content {
    margin-bottom: 20px;
    font-size: 15px;
    letter-spacing: 0.6px;
  }
  .footer__item-icon {
    width: 40px;
    height: 40px;
  }
  .footer__item-btns {
    gap: 20px;
  }
  .footer__map {
    height: 420px !important;
    margin: 0 -20px;
  }
  .header__btn-footer {
    margin: 0;
  }

  .footer__wrap {
    padding: 0 20px;
  }
  .footer__item-text:not(:last-child) {
    margin-bottom: 10px;
}
.footer__contact-phone {
  font-size: 16px;
}
.footer__contact-name {
  flex: 0 0 160px;
}
.footer__contact-item {
  gap: 12px;
  align-items: baseline;
}
.footer__contact-item:not(:last-child) {
  margin-bottom: 20px;
}
.footer__contact-item--wrap {
  gap: 7px;
}
.footer__information {
  padding: 15px 0;
}
}
