.header {
  background: url("../img/main-bg.webp") no-repeat center;
  background-size: cover;
  height: 100vh;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);

  // height: 1000px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);


  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    // height: 1000px;
    padding: 45px 0;
    padding-bottom: 60px;
  }
  &__logo {
    // width: calc(180px + 252 * (100vw / 1920));
    width: 432px;
    height: auto;
    img {
      object-fit: contain;
    }
  }
  &__title {
    position: relative;
    // overflow: hidden;
    font-weight: 800;
    font-size: calc(25px + 35 * (100vw / 1920));
    line-height: 100%;
    letter-spacing: 0.02em;
    color: #000;
    margin-bottom: 45px;
    // max-width: 800px;
    width: 70%;
  }
  &__text {
    font-weight: 700;
    font-size: calc(10px + 15 * (100vw / 1920));
    line-height: 110%;
  }
}

#canvasBlik {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
@media(max-width: 1200px) {
  .header__title {
    width: 65%;
  }
}
@media(max-width: 540px) {
  .header {
    background: url("../img/main-bg-mobile.png") no-repeat top center;
    background-size: cover;
    box-shadow: none;
    height: 591px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  }

  .header__logo {
    width: 180px;
  }
  .header__wrapper {
    padding: 18px 0 27px;
    padding-bottom: 40px;
    height: 591px;
  }

  .header__title {
    font-size: 25px;
    margin-bottom: 15px;
    width: 100%;
  }
  .header__text {
    font-size: 12px;
  }
}

@media(max-width: 378px) {
  .header {
    box-shadow: none;
  }
}


