// margin-bottom: calc(20px + 38 * (100vw / 1920));
.about {
  padding: 55px 0;

  &__content {
    margin-bottom: 59px;
  }

  &__content-list {
  }

  &__content-icon {
    width: 7px;
    height: 7px;
    background: #000;
    border-radius: 50%;
    flex-shrink: 0;
    margin-top: 12px;
  }

  &__content-img {
    display: none;
  }

  &__content-item {
    list-style: none;
    display: flex;
    align-items: start;
    gap: 17px;
    margin-left: 15px;
  }

  &__content-item:not(:last-child) {
    margin-bottom: 49px;
  }

  &__content-text {
    line-height: 110%;
    font-weight: 600;
    font-size: calc(15px + 23 * (100vw / 1920));
    // font-size: 38px;
    list-style: none;
    margin-bottom: 53px;
    margin-left: 0px;
    .about__content-icon {
      display: none;
    }
  }

  &__line {
    width: 100%;
    margin-bottom: 58px;
    height: 1.5px;
    background: #bcbcba;
    background: #babab8;
  }

  &__item-title {
    font-weight: 600;
    font-size: calc(22px + 16 * (100vw / 1920));
    line-height: 110%;
    margin-bottom: 56px;
  }

  &__item-wrap{
    display: flex;
    gap: 40px;
    align-items: center;
  }

  &__item-img {
    flex: 0 1 770px;
  }

  &__item-content {
    flex: 0 1 735px;
    line-height: 150%;
  }

  &__item-text:first-child {
    margin-bottom: 30px;
  }
  &__item-text:nth-child(2) {
    margin-bottom: 10px;
  }

  &__list {
    margin-left: 30px;
  }

  &__list-item {
    span {
      font-weight: 600;
      font-family: var(--second-family);
    }
  }
  &__item-title--mobile {
    display: none;
  }
}

@media(max-width: 1000px) {
  .about__item-wrap {
    align-items: stretch;
  }
}

@media(max-width: 768px) {
  .about__item-wrap {
    font-size: 15px;
  }
  .about__item-wrap{
    gap: 20px;
  }
  .about__item-content {
    line-height: 140%;
  }
}

@media(max-width: 540px) {
  // анимация
  @keyframes example {
    0% { transform: rotate(0deg); }
    50% { transform: rotate(-45deg); }
    100% { transform: rotate(0deg); }
  }
  .about {
    padding: 29px 0;
    padding-bottom: 37px;
}
.about__content {
  margin-left: -10px;
  margin-bottom: 46px;
}
  .about__content-text {
    font-weight: 400;
    font-size: 15px;
  }
  .about__content-text .about__content-icon {
    display: block;
  }
  .about__content-icon {
    width: 30px;
    height: 30px;
    background: transparent;
    border-radius: none;
    flex-shrink: 0;
    margin-top: -2px;
    animation: example 2s infinite;
  }
  .about__content-img {
    display: block;
  }
  .about__content-item {
    margin-left: 0;
    span {
      line-height: 138%;
    }
  }
  .about__content-item:not(:last-child) {
    margin-bottom: 22.5px;
}
.about__line {
  display: none;
}
.about__item-wrap {
  display: block;
  // box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
background: #fff;

}
  .about__item-title--mobile {
    display: block;
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
    margin-bottom: 28px;
    span {
      color: #fff;
    }
  }
  .about__item-title--desctop {
    display: none;
  }
  .about__item-img {
    height: 232px;
  }
  .about__item-content {
    margin-top: -28px;
    padding: 0 13px;
    padding-bottom: 35px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    margin-left: 3px;
    margin-right: 3px;
}
.about__item-text:nth-child(2) {
  line-height: 138%;
  margin-bottom: 24px;
}
.about__item-text {
  line-height: 138%;
}
.about__list {
  margin-top: 6px;
}
.about__list-item:not(:last-child) {
  margin-bottom: 1px;
}
.about__list {
  margin-left: 23px;
}
}



